
import "../autd.css"
import "./detail.less"
import {connect} from "react-redux"
import { http } from "../utils";
import {  getGood} from "../actions";
import { Button ,WhiteSpace ,WingBlank,Picker, List,Stepper ,InputItem,ActionSheet} from "antd-mobile";
import Toast from "../components/Toast";
const isIPhone = new RegExp('\\biPhone\\b|\\biPod\\b', 'i').test(window.navigator.userAgent);
let wrapProps;
if (isIPhone) {
  wrapProps = {
    onTouchStart: e => e.preventDefault(),
  };
}
@connect(
    state=>{
        return {
            data:state.user
        }
    }
)
export class Detail extends React.Component{
    state={
        colors : [
            {
                label:
                (<div>
                  
                  <span>无糖</span>
                </div>),
                value: '无糖',
              },
              {
                label:
                (<div>
                  <span>少糖</span>
                </div>),
                value: '少糖',
              },
              {
                label:
                (<div>
                  <span>多糖</span>
                </div>),
                value: '多糖',
              }
        ],
        hots : [
            {
                label:
                (<div>
                  
                  <span>热</span>
                </div>),
                value: '热',
              },
              {
                label:
                (<div>
                  <span>常温</span>
                </div>),
                value: '常温',
              },
              {
                label:
                (<div>
                  <span>去冰</span>
                </div>),
                value: '去冰',
              },{
                label:
                (<div>
                  <span>少冰</span>
                </div>),
                value: '少冰',
              },{
                label:
                (<div>
                  <span>多冰</span>
                </div>),
                value: '多冰',
              }
        ],
        sizes: [
            {
                label:
                (<div>
                  
                  <span>小杯</span>
                </div>),
                value: '小杯',
              },
              {
                label:
                (<div>
                  <span>中杯</span>
                </div>),
                value: '中杯',
              },
              {
                label:
                (<div>
                  <span>大杯</span>
                </div>),
                value: '大杯',
              }
        ],
        size:{
            label:
            (<div>
                <span>大杯</span>
            </div>),
            value: '大杯',
        },
        colorValue : {
            label:
            (<div>
                <span>多糖</span>
            </div>),
            value: "多糖",
        },
        hot:{
            label:
                (<div>
                  <span>多冰</span>
                </div>),
                value: '多冰',
        },
        number : 1,
        beizhu : "",
       
    } 
    dataList = [
        { url: 'OpHiXAcYzmPQHcdlLFrc', title: '发送给朋友' },
        { url: 'wvEzCMiDZjthhAOcwTOu', title: '新浪微博' },
        { url: 'cTTayShKtEIdQVEMuiWt', title: '生活圈' },
        { url: 'umnHwvEgSyQtXlZjNJTt', title: '微信好友' },
        { url: 'SxpunpETIwdxNjcJamwB', title: 'QQ' },
    ].map(obj => ({
        icon: <img src={`https://gw.alipayobjects.com/zos/rmsportal/${obj.url}.png`} alt={obj.title} style={{ width: 36 }} />,
        title: obj.title,
    }))
    changs=(value)=>{
        this.setState({
            colorValue : value
        })
    }
    changhot=(value)=>{
        this.setState({
            hot : value
        })
    }
    changsize=(value)=>{
        this.setState({
            size : value
        })
    }
    changnum=(value)=>{
        this.setState({
            number : value
        })
    }
    changbei=(value)=>{
        console.log(value)
        this.setState({
            beizhu : value
        })
    }
    shop=()=>{
        let {dispatch,history,data:{goods}} = this.props;
        if(localStorage.tel){
            http.get("/create",{params:{
                tel : localStorage.tel,
                number : this.state.number,
                size : this.state.size.value,
                hot : this.state.hot.value,
                sweet: this.state.colorValue.value,
                beizhu:this.state.beizhu,
                dataId : goods[0].id
            }}).then(res=>{
                if(res.data.type==1){
                    Toast.success("加入购物车成功",1000)
                }
                console.log(res)
            })
        }else{
            Toast.error("请先登录",1000)
        }
        
        console.log(this.state)
    }
    shops=()=>{
        let {dispatch,history,data:{goods}} = this.props;
        if(localStorage.tel){
            http.get("/create",{params:{
                tel : localStorage.tel,
                number : this.state.number,
                size : this.state.size.value,
                hot : this.state.hot.value,
                sweet: this.state.colorValue.value,
                beizhu:this.state.beizhu,
                dataId : goods[0].id
            }}).then(res=>{
                if(res.data.type==1){
                    history.push("/shoppingcar")
                }
            })
        }else{
            Toast.error("请先登录",1000)
        }
        
        console.log(this.state)
    }
    showShareActionSheet = () => {
        ActionSheet.showShareActionSheetWithOptions({
          options: this.dataList,
          // title: 'title',
          message: '分享starbucks',
        },
        (buttonIndex) => {
          this.setState({ clicked1: buttonIndex > -1 ? this.dataList[buttonIndex].title : 'cancel' });
          // also support Promise
          return new Promise((resolve) => {
            setTimeout(resolve, 1000);
          });
        });
      }
    render(){
        let {data:{good}} = this.props;
        var dom=null;
        if(good.preview){
           if(!~good.preview.indexOf("https://www.starbucks.com.cn/")){
            good.preview = "https://www.starbucks.com.cn/"+good.preview
          }
          dom=(<div><img src={good.preview} style={{width:"100%"}} />
          <p className="title">{good.title}</p>
          <p className="body">{good.body}</p> <WhiteSpace size="xl" />
          <List>
          <List.Item
              wrap
              extra={
                  <Stepper
                  style={{ width: '100%', minWidth: '80px' }}
                  showNumber
                  max={100}
                  min={1}
                  value={this.state.number}
                  onChange={this.changnum}
                  />}
              >
              数量
              </List.Item></List>
          <WhiteSpace size="xl" />
          <WingBlank className="bb">
              备注：<InputItem type="text"  className="beizhu" placeholder="如有特殊需求，请备注" value={this.state.beizhu} onChange={this.changbei}/>
          </WingBlank>
          <WhiteSpace size="xl" />
          <WingBlank>
          <Picker
              data={this.state.colors}
              value={this.state.colorValue}
              onChange={this.changs}
              cols={1}
              >
              <List.Item arrow="horizontal">甜度</List.Item>
           </Picker>
           </WingBlank>
          <WhiteSpace size="xl" />
          <WingBlank>
          <Picker
              data={this.state.hots}
              value={this.state.hot}
              onChange={this.changhot}
              cols={2}
              >
              <List.Item arrow="horizontal">温度</List.Item>
           </Picker>
           </WingBlank>
           <WhiteSpace size="xl" />
          <WingBlank>
          <Picker
              data={this.state.sizes}
              value={this.state.size}
              onChange={this.changsize}
              cols={3}
              >
              <List.Item arrow="horizontal">规格</List.Item>
           </Picker>
           </WingBlank>
           <WhiteSpace size="xl" /><WhiteSpace size="xl" /><WhiteSpace size="xl" />
          <div className="foot">
              <WingBlank size="xl">
                  <Button type="primary" inline style={{ marginRight: '4px' }} className="am-button-borderfix" onClick={this.shops}>立即购买</Button>
              <Button type="ghost" inline style={{ marginRight: '4px' }} className="am-button-borderfix" onClick={this.shop}>加入购物车</Button>
              <Button type="ghost" inline style={{ marginRight: '4px' }} className="am-button-borderfix" onClick={this.showShareActionSheet}>分享</Button>                    
              </WingBlank>
          </div></div>)
        }
        return (
            <div>
                <div className="tx">
                    <a onClick={()=>this.props.history.go(-1)}>
                        &nbsp;&nbsp;<img src="https://www.starbucks.com.cn/assets/icons/icon-chevron-left.svg"/>
                    </a>
                </div>
                {dom}
            </div>
            
        )
    }
}



