import { Carousel, WingBlank } from 'antd-mobile';
import { Link } from "react-router-dom"
export class Banner extends React.Component {
        state={
            data:[
                
            ]
        }
    
    componentDidMount(){
    // simulate img loading
        setTimeout(() => {
        this.setState({
            data: [
            {path:"/cicle",url:"https://www.starbucks.com.cn/assets/images/featured/2019spring/kv-merchandise-mobile.jpg"},
            {path:"/cicle",url:"https://www.starbucks.com.cn/assets/images/featured/2019spring/kv-bev-1-mobile.jpg"},
            {path:"/cicle",url:"https://www.starbucks.com.cn/assets/images/featured/2019spring/kv-cold-brew-mobile.jpg"},
            {path:"/cicle",url:"https://www.starbucks.com.cn/assets/images/featured/2019spring/kv-merchandise-mobile.jpg"}],
        });
        }, 100);
    }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite={true}
          
        >
          {this.state.data.map(val => (
            <Link
              key={val.url}
              to={val.path}
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={val.url}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </Link>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}




