import "../autd.css"
import "./search.less"
import {SearchBar} from "antd-mobile"
export class Search extends React.Component{
    state = {
        value : ''
    }
    cancel=()=>{
        let {history} =this.props;
        history.go(-1);
    }
    onChange=(value)=>{
        this.setState({
            value
        })
    }
    submit=()=>{
        let { history } = this.props;
        localStorage.keys = this.state.value;
        history.push("/searchlist")
    }
    render(){
        return(
            <div>  
                 <SearchBar
                    value={this.state.value}
                    placeholder="Search"
                    onSubmit={this.submit}
                    onCancel={this.cancel}
                    showCancelButton
                    onChange={this.onChange}
                />
        <div className="cbacks">
                <h2>大家都在搜</h2>
                <ul className="new">
                    <li>
                        <div>
                            <img src="https://www.starbucks.com.cn/assets/images/featured/2019spring/cherry-blossom-flavored-macchiato.jpg" />
                        </div>
                        <p>
                        樱花浪漫玛奇朵
                        </p>
                    </li>
                    <li>
                        <div>
                            <img src="https://www.starbucks.com.cn/assets/images/featured/2019spring/rum-vanilla-flavored-macchiato.jpg" />
                        </div>
                        <p>
                        朗姆香草风味玛奇朵
                        </p>
                    </li>
                    <li>
                        <div>
                            <img src="https://www.starbucks.com.cn/assets/images/featured/2019spring/spring-cherry-blossoming-mousse-cake.jpg" />
                        </div>
                        <p>
                        樱之花意慕斯蛋糕
                        </p>
                    </li>
                    <li>
                        <div>
                            <img src="https://www.starbucks.com.cn/assets/images/featured/2019spring/coconut-white-chocolate-tart.jpg" />
                        </div>
                        <p>
                        椰香绵绵挞
                        </p>
                    </li>
                    <li>
                        <div>
                            <img src="https://www.starbucks.com.cn/assets/images/featured/2019spring/matcha-chocolate-mousse-cake.jpg" />
                        </div>
                        <p>
                        抹茶森林生巧蛋糕
                        </p>
                    </li>
                    <li>
                        <div>
                            <img src="https://www.starbucks.com.cn/assets/images/featured/2019spring/mixed-nuts-oat-yoghurt-flavor-cake.jpg" />
                        </div>
                        <p>
                        坚果燕麦酸奶风味蛋糕
                        </p>
                    </li>
                </ul>
            </div>
            </div>
        )
        
    }
}


