
const defaultState = {
    userinfo:{},
    goods:[],
    good:{},
    car:[],
    total:0,
    carNum:0,
    jiesuan : [],
    quan:false,
    address:[]
}
export default (state=defaultState,action) => {
    switch(action.type){
        case "user":
            return {...state,...{userinfo:action.data}}
            break;
        case "getGoods":
            return {...state,...{goods:action.goods}}
            break;
            case "getGood":
            return {...state,...{good:action.good}}
            break;
        case "reverseGoods":
        return {...state,...{goods:state.goods.reverse()}}
        break;
        case "getCar":
        return {...state,...{car:action.car,total:0,carNum:0}}
        break;

        case "setCarList":
        var total = 0;
        var carNum = 0;
        var quan = true; 
        var car = state.car.map((item,index)=>{
            if(item.dataId==action.dataId){
                item.check = action.check;
            }
            if(item.check){
                carNum += item.number
                total += item.number * 520;
            }
            if(!item.check){
                quan = false;
            }
            return item;
        })
        return {...state,...{car,total,quan,carNum}}
        break;

        case "address":
        return {...state,...{address:action.address}}
        break;

        case "add":
        var car = state.car.map((item,index)=>{
            if(item.dataId==action.dataId){
                item.number = action.number +1;
            }
            return item;
        })
        return {...state,...{car}}
        break;

        case "reduce":
        var car = state.car.map((item,index)=>{
            if(item.dataId==action.dataId){
                item.number = action.number -1;
            }
            return item;
        })
        return {...state,...{car}}
        break;
        case "jiesuan":
        var jiesuan = state.car.filter((item,index)=>{
            return item.check;
        })
        return {...state,...{jiesuan,quan:false}}
        break;
        case "setQuan":
        var total = 0;
        var carNum = 0;
        var car = state.car.map((item,index)=>{
            item.check = action.quan;
            if(item.check){
                carNum +=item.number
                total += item.number * 520;
            }
            return item;
        })
        return {...state,...{car,total,quan:action.quan,carNum}}
        break;
        case "deletes":
        var car = state.car.filter((item,index)=>{
            return item.check == false
        })
        return {...state,...{car}}
        default:
            return state;
            break;
    }
}



