


import React, {Component } from "react";
import { PullToRefresh} from 'antd-mobile';
import {Link} from "react-router-dom"
import {connect} from "react-redux"
import { reverseGoods,getGood } from "../../actions";

import "./index.less"
@connect()
export class GoodList extends Component{
    state = {
        refreshing:false
    }
    detail=(key)=>{
        let {dispatch} = this.props;
        dispatch(getGood({url:"/search",key}));
    }
    render(){
        let {goods,dispatch} = this.props;
        return (
            <div>
                <PullToRefresh
                    damping={60}
                    ref="goods"
                    style={{
                        overflow: 'auto',
                    }}
                    direction={ 'down' }
                    indicator={ { deactivate: '上拉可以刷新' }}
                    refreshing={this.state.refreshing}
                    onRefresh={() => {    // 下拉刷新 
                        this.setState({ refreshing: true });
                        setTimeout(() => {
                            dispatch(reverseGoods())
                            this.setState({ refreshing: false });
                        }, 2000);
                    }}
                    className = "bck"
                >
                     <ul>{
                        goods.map((item,index)=>{
                            item.url="https://www.starbucks.com.cn/"+item.preview;
                            return (
                               <li key={index} onClick={()=>this.detail(item.id)}>
                                    
                                        <Link to={item.id}>
                                        <div className="moveIn"  >
                                            <img src={item.url} alt="aa"/>
                                        </div>
                                            <h4>{item.title}</h4>
                                            <p>价格 : ￥520</p>
                                        </Link>
                                    
                                
                                </li>
                                
                            )
                        })
                    }</ul>
                </PullToRefresh>
            </div>

        )
    }
}


