import "./home.less"
import { Banner } from "../components/Banner/Banner";
import { Link } from "react-router-dom"

import { Footer } from "../components/Footer/Footer";
export class Home extends React.Component{
    render(){
        return (
            <div>
            <div className="tx">心情惬意，来杯咖啡吧 ☕</div>
            <Banner />
            <ul className="uls">
                <li>
                    <img src="https://www.starbucks.com.cn/images/pages/tmall-0222-cn.jpg" />
                </li>
                <li>
                    <img src="https://www.starbucks.com.cn/images/pages/starbucks-design-studio-web-china.jpg" />
                </li>
                <li>
                    <img src="https://www.starbucks.com.cn/images/pages/homepage-career-cn.jpg" />
                </li>
                <li className="li">
                    <h2>
                    星享俱乐部
                    </h2>
                    <p>
                        开启您的星享之旅，星星越多、会员等级越高、好礼越丰富。
                    </p>
                    <p className="p">
                        <Link to="/user/register">注册</Link>
                        <Link to="/user/login">登录</Link>
                    </p>
                    <div className="img">
                        <img src="https://www.starbucks.com.cn/assets/images/logo-msr-new.svg" />
                    </div>
                </li>
            </ul>
            <Footer />
            </div>
        )
    }
}



