import {NavLink} from "react-router-dom"
import "./nav.less"

export class UserNav extends React.Component{
    constructor(props){
        super(props)
        this.state={
            list:[
                {path:"/user/login",text:"登录"},
                {path:"/user/register",text:"注册"}
            ]
        }
    }
    render(){
        let domeList = this.state.list.map(({path,text})=>
            <NavLink activeClassName="active" to={path} key={path}>
                {text}
            </NavLink>
        )
        
        return (<div className="nav">{domeList}</div>)
    }
}


