
import {connect} from "react-redux"
import { http } from "../utils";
import {  getCar ,setCarList,setQuan ,add ,reduce,deletes,jiesuan,address} from "../actions";
import {Link} from "react-router-dom";
import {List,Stepper,Checkbox,Button,InputItem,WhiteSpace,WingBlank,Card} from "antd-mobile"
// const CheckboxItem = Checkbox.CheckboxItem;
import "./shoppingcar.less"
import axios from "axios"
import Toast from "../components/Toast";
  
@connect(
    state=>{
        return {
            data:state.user,
        }
    }
)

export class Jiesuan extends React.Component{
    state={
        pro:[],
        city:[],
        town:[],
        proT: "",
        cityT:'',
        townT:''
    }
    componentWillMount(){
        let {dispatch} =this.props
        this.props.dispatch(address());
        axios.get("http://api.yytianqi.com/citylist/id/2").then((res)=>{
           this.setState({
               pro : res.data.list
           })
        })
    }
    pro(e){
        var citys=this.state.pro.filter((item)=>{
            return  item.city_id==e.target.value
        })
        this.setState({
            city:citys[0].list,
            proT:e.target.value,
            town:[]
        })
    }
    city(e){
        var citys=this.state.city.filter((item)=>{
            return  item.city_id==e.target.value
        })
        if(citys[0].list){
            this.setState({
                town:citys[0].list,
                cityT:e.target.value
            })
        }else{
            this.setState({
                cityT : e.target.value ,
                town:[]
            })
        }
        
    }
    town(e){
        this.setState({
            townT:e.target.value
        })
    }
    jiesuan=()=>{
        this.props.dispatch(jiesuan());
    }
    qiehuan=()=>{
        this.props.history.push("/address")
    }
    render(){
        let {data:{jiesuan,carNum,total,address}} = this.props;
        let dom = jiesuan.map((good,i)=>{
            let{beizhu,dataId,hot,number,size,sweet,books:{body,title,preview}}=good
            if(!~preview.indexOf("https://www.starbucks.com.cn/")){
                preview="https://www.starbucks.com.cn/"+preview;
            }
            return (
                <li key={dataId}className="car">
                    <div className="left">
                        <img src={preview} />
                    </div>
                    <div className="right">
                    <Link to={dataId}><h2>{title}</h2></Link>
                        <p>
                            {body}
                        </p>
                
                        <span>￥520</span>
                        <span>{size}</span>
                        <span>{sweet}</span>
                        <span>{hot}</span>
                        <span>数量：{number}</span>
                    </div>
                    <p style={{textIndent:".3rem"}}>备注：{beizhu}</p>
                </li>    
                )
        })
        var pros = null;
        if(this.state.pro.length>0){
            pros=this.state.pro.map((value,index)=>{
                return (
                    <option value={value.city_id} key ={index} >{value.name}</option>
                )
            })  
        }
        var citys = null;
        if(this.state.proT.length>0){
            citys=this.state.city.map((value,index)=>{
                return (
                    <option value={value.city_id} key ={index} >{value.name}</option>
                )
            })
        }    
        var towns = null;
        if(this.state.town.length>0){
            towns=this.state.town.map((value,index)=>{
                return (
                    <option value={value.city_id} key ={index} >{value.name}</option>
                )
            })
        }    
        return (
            
            <div>
                <div className="tx">
                <a onClick={()=>this.props.history.go(-1)}>
                        &nbsp;&nbsp;<img src="https://www.starbucks.com.cn/assets/icons/icon-chevron-left.svg"/>
                    </a>
                    结算
                </div>
                <WingBlank size="lg">
                    <WhiteSpace size="lg" />
                    <Card>
                    <Card.Header
                        title="收货地址"
                        thumb="https://gw.alipayobjects.com/zos/rmsportal/MRhHctKOineMbKAZslML.jpg"
                    />
                    <Card.Body>
                        <div className="address">
                            请选择
                            <div style={{ width:"25%" }}><select onChange={this.pro.bind(this)} value={this.state.proT}>{pros}</select></div>
                            <div style={{ width:"25%" }}><select onChange={this.city.bind(this)} value={this.state.cityT}>{citys}</select></div>
                            <div style={{ width:"25%" }}><select onChange={this.town.bind(this)} value={this.state.townT}>{towns}</select></div>
                        </div>
                    </Card.Body>
                    <Card.Body>
                    <InputItem 
                    type="test"
                    placeholder="address"
                    className="phone"
                >
                详细地址
                </InputItem>
                    </Card.Body>
                    <Card.Body>
                    <InputItem 
                    type="phone"
                    placeholder="phone"
                    className="phone"
                >
                手机号
                </InputItem>
                    </Card.Body>
                    <Card.Footer content="收货人" extra={<input style={{"border":0,"padding":0,"backgroundColor":"#eee"}}/>} />
                    </Card>
                    <WhiteSpace size="lg" />
                </WingBlank>
                
                <WhiteSpace />
                <div className="address">
                </div>
                <ul>
                    {dom}
                    
                </ul>
                <div className="cars">
                    <div key="1" >
                        <div style={{ width:"30%" }}></div>
                        <div style={{ width:"15%" }}>共{carNum}件</div>
                        <div style={{ width:"15%" }}>￥{total}</div>
                        <div style={{ width:"18%",display:"inline-block",lineHeight:".6rem" }}><Button type="warning" size="small" onClick={this.jiesuan}>付款</Button></div>
                    </div>
                </div>
            </div>
        )
    }
}





