import { UserNav } from "../components/Usernav/Nav";
import "./login.less"
import { http } from "../utils";
import { List, InputItem } from 'antd-mobile';
import Toast from "../components/Toast";
import VCode from "../components/Tupian";
import { Footer } from "../components/Footer/Footer";
export class Login extends React.Component{
        state = {
            hasError: false,
            hasErrors: false,
            value: '',
            password : "",
            x : '',
            y : '',
            src : "https://www.starbucks.com.cn/assets/icons/icon-hide-password.svg",
            flag : false,
            type : "password",
            data : [],
            yzm : '',
            bool : false
        
    }
    getData = (data)=>{
        this.setState({
            data
        })
    }
    onBlur = () => {
        if (this.state.hasError||this.state.value.length==0) {
            Toast.error('请输入11位手机号', 1000)
        }else{
            Toast.success('手机号格式正确', 1000)
        }
        this.setState({
            x: "",
            });
    }
    onBlurs = () => {
        if (this.state.hasErrors||this.state.password.length==0) {
            Toast.error('必须包含大小写字母、数字', 1000)
        }else{
            Toast.success('密码格式正确', 1000)
        }
        this.setState({
            y: "",
            });
    }
    onFocus = ()=>{
        this.setState({
            x: "手机号",
            });
    }
    onFocuss = ()=>{
        this.setState({
            y: "密码",
            });
    }
    onChange = (value) => {
        if (value.replace(/\s/g, '').length <11) {
            this.setState({
                hasError: true,
            });
            } else {
                let val = value.replace(/\s/g, '')
                let tests = /^((13[0-9])|(14[5,7])|(15[0-3,5-9])|(17[0,3,5-8])|(18[0-9])|166|198|199|(147))\d{8}$/;
                var re = new RegExp(tests);
                if(!re.test(val)){
                    this.setState({
                        hasError: true,
                    });
                }else{
                    this.setState({
                        hasError: false,
                    });
                }
            }
            this.setState({
            value,
            });
    }
    
    onChanges = (password) => {
        let testss =/^.*(?=.{6,16})(?=.*\d)(?=.*[A-Z])(?=.*[a-z]).*$/;
        var re = new RegExp(testss);
    if (re.test(password)) {
        this.setState({
        hasErrors: false,
        });
    } else {
        this.setState({
            hasErrors: true,
            });
    }
    this.setState({
        password,
    });
    }
    onChangess = (yzm)=>{
        this.setState({
            yzm
        });
    }
    onClicks = () =>{
        if(this.state.flag){
            this.setState({
                src: "https://www.starbucks.com.cn/assets/icons/icon-hide-password.svg",
                type : "password"
            });
        }else{
            this.setState({
                src: "https://www.starbucks.com.cn/assets/icons/icon-show-password.svg",
                type : "text"
            });
        }
        this.setState({
            flag : !this.state.flag
        })
    }
    yz=()=>{
        if(this.state.yzm.length==4){
            let arr=[];
            arr=this.state.data.map((v)=>
                String.fromCharCode(v*1 > 57 && v*1 < 84 ? v*1 + 7 : ( v*1 < 57 ? v*1 : v*1 + 13 ))
            )
            let str=arr.join("").toLowerCase();
            var isTrue=false;
            if(this.state.yzm.toLowerCase()==str){
                this.setState({
                    bool : true
                })
                isTrue=true
            }
        }
        if(this.state.bool||isTrue){
            Toast.success('验证成功', 1000)
        }else{
            Toast.error('验证失败', 1000)
        }
    }
    login=()=>{
        const {history } = this.props;
        if(this.state.bool&&(!this.state.hasError)&&(!this.state.hasErrors)){
            var that =this;
            http.get("/login",{
                params :{
                    tel : that.state.value.replace(/\s/g, ''),
                    password : that.state.password
                }
            }).then(res=>{
                console.log(res);
                if(res.data.code==1) {
                    localStorage.tel=that.state.value.replace(/\s/g, '')
                    Toast.success('很高兴遇见你', 1000)
                    setTimeout(() => {
                       history.push('/home') 
                    }, 500);
                }
                else Toast.error('手机号或密码错误', 1000)
            })
        }
    }
    render(){
        return(
            <div>
                <div className="tx">登录或创建一个新帐户 🌟</div>
               <UserNav /> 
               <div className="backinp">
                    <div className="number">
                            &nbsp;&nbsp;&nbsp;{this.state.x}
                    </div>
                    <List  className="number">
                        <InputItem 
                            type="phone"
                            placeholder="手机号"
                            onBlur={this.onBlur}
                            onChange={this.onChange}
                            value={this.state.value}
                            onFocus={this.onFocus}
                            className="phone"
                        >
                        </InputItem>
                    </List>
                    <div className="number">
                        &nbsp;&nbsp;&nbsp;{this.state.y}
                    </div>
                    <List  className="number" id="number">
                        <InputItem
                            onBlur={this.onBlurs}
                            onChange={this.onChanges}
                            value={this.state.password}
                            onFocus={this.onFocuss}
                            type={this.state.type}
                            placeholder="******"
                            className="phone"
                            >
                        </InputItem>
                        <div className="kan" onClick={this.onClicks}>
                                <img src={this.state.src}/>
                        </div>
                    </List>
                    <div className="number yam">
                        <p>请您输入下图中的验证码：</p>
                        <VCode getData={this.getData} />
                        <InputItem 
                            type="text"
                            placeholder="看不清，点击切换"
                            className="phone yzminp"
                            onChange={this.onChangess} 
                            value={this.state.yzm}
                            >
                        </InputItem>
                        <button  className="button" onClick={this.yz} >验证</button>
                    </div>
                    <div className="big number">
                        <button  className="buttons" onClick={this.login} >登录</button>
                    </div>
                </div>
                <Footer />
            </div>
            
        )
    }
}




