
import {http} from "../utils";
import axios from "axios";
export const user =({url,tel})=>{
    return http.get(url,{
        params : {
            tel
        }
    }).then(res=>{
        return {
            type : "user",
            data : res.data[0]
        }
    })
}

export const getGood = ({url,key}) =>{
    return http.get(url,{
        params : {
            key
        }
    }).then(res=>{
        return {
            type:"getGood",
            good:res.data[0]
        }
    })
}
export const getGoods = goods =>{
    return {
        type:"getGoods",
        goods
    }
}

export const reverseGoods = () =>{
    return {
        type:"reverseGoods",
        
    }
}

export const getCar = car =>{
    return {
        type:"getCar",
        car
    }
}

export const setCarList = ({check,dataId}) =>{
    return {
        type:"setCarList",
        check,
        dataId
    }
}

export const setQuan = (quan) =>{
    return {
        type:"setQuan",
        quan
    }
}
export const jiesuan = () =>{
    return {
        type:"jiesuan"
    }
}
export const add = (dataId,number,tel) =>{
    http.get("/updatenum",{
        params : {
            tel ,
            dataId,
            number : number+1
        }
    }).then(res=>{
        console.log(res)
    })
    return {
        type:"add",
        dataId,
        number,
    }
}
export const reduce = (dataId,number,tel) =>{
    http.get("/updatenum",{
        params : {
            tel,
            dataId,
            number : number-1
        }
    }).then(res=>{
    })
    return {
        type:"reduce",
        dataId,
        number,
    }
}
export const deletes = (car)=>{
    car.forEach((item) => {
        if(item.check){
            http.get("/delone",{
                params : {
                    tel : item.tel,
                    dataId : item.dataId
                }
            }).then(res=>{
            })
        }
    });
    return{
        type: "deletes"
    }
}

export const address=()=>{
    return http.get("/address",{params:{tel:localStorage.tel}}).then(res=>{
        return{
            type: "address",
            address:res.data
        }
    })
}


