import {Route,Switch,Redirect} from "react-router-dom"
import { Home } from "./Home";
import { Detail } from "./Detail";
import { Shoppingcar } from "./Shoppingcar";
import { Cicle } from "./Cicle";
import { Register } from "./Register";
import { Login } from "./Login";
import { UserDetail } from "./UserDetail";
import { CicleList } from "./List";
import { Search } from "./Search";
import { SearchList } from "./SeachList";
import { Jiesuan } from "./Jiesuan";
import { Guide } from "./Guide";
export class Index extends React.Component{
    render(){
        return (
            <div>
                <Switch>
                <Route path="/"  exact   component={Guide}  />
                    <Route path="/searchlist" component={SearchList}/>
                    <Route path="/search" component={Search}  />
                    <Route path="/jiesuan" component={Jiesuan}  />
                    <Route path="/home" component={Home} />
                    <Route path="/shoppingcar" component={Shoppingcar} />
                    <Route path="/menu" component={Detail} />
                    <Redirect from="/user" exact={true} to="/user/userdetail"  />
                    <Route path="/cicle" exact={true} component={Cicle} />
                    <Route path="/cicle/beverages" component={CicleList}/>
                    <Route path="/cicle/food" component={CicleList}/>
                    <Route path="/cicle/merchandise" component={CicleList}/>
                    <Route path="/cicle/coffee" component={CicleList}/>
                    <Route path="/user/userdetail" component={UserDetail} />
                    <Route path="/user/login" component={Login} />
                    <Route path="/user/register" component={Register} />
                </Switch>
            </div>
        )
    }
}


