import {NavLink} from "react-router-dom"
import "../Usernav/nav.less"

export class UserNav extends React.Component{
    constructor(props){
        super(props)
    }
    
    render(){
        let domeList = this.props.list.map(({path,text})=>
            <NavLink activeClassName="active" to={path} key={path}>
                {text}
            </NavLink>
        )
        return (<div className="nav" >{domeList}</div>)
    }
}


