import "./cicle.less"
import "../autd.css"
import {connect} from "react-redux"
import { http } from "../utils";
import {  getGoods } from "../actions";
import {Button} from "antd-mobile"
import { GoodList } from "../components/GoodsList";
import {Link} from "react-router-dom"
@connect(
    state=>{
        return {
            data:state.user
        }
    }
)
export class SearchList extends React.Component{
    componentWillMount(){
        let {dispatch} = this.props;
        var key = localStorage.keys;
        http.get("/searchs",{params:{key}}).then(res=>{
            dispatch(getGoods(res.data));
        })
    }
    render(){
        let {data:{goods}} = this.props;
        return (
            <div>
                <div className="tx">
                <a onClick={()=>this.props.history.go(-1)}>
                        &nbsp;&nbsp;<img src="https://www.starbucks.com.cn/assets/icons/icon-chevron-left.svg"/>
                    </a>
                    {localStorage.keys}
                </div>
                <GoodList goods={goods} />
                <div className="sou" >
                    <Link to="/search">
                    <Button type="ghost" size="small" >
                        搜索商品
                    </Button></Link>
                </div>
                
            </div>
        )
    }
}




