import {NavLink} from "react-router-dom"
import "./index.less"

export class Userd extends React.Component{
    constructor(props){
        super(props)
        this.state={
            list:[
                {path:"/user/userdetail",text:"待付款",icon:"iconfont icon-daifukuan"},
                {path:"/user/userdetail",text:"待发货",icon:"iconfont icon-daifahuo"},
                {path:"/user/userdetail",text:"待收货",icon:"iconfont icon-daishouhuo"},
                {path:"/user/userdetail",text:"评价",icon:"iconfont icon-pingjia-tianchong"}
            ]
        }
    } 
    render(){
        let domeList = this.state.list.map(({path,text,icon})=>
            <NavLink activeClassName="active" to={path} key={icon}>
                <i className={icon}></i>
                <p>{text}</p>
            </NavLink>
        )
        
        return (<div className="userd">{domeList}</div>)
    }
}


