
import {connect} from "react-redux"
import { http } from "../utils";
import {  getCar ,setCarList,setQuan ,add ,reduce,deletes,jiesuan,getGood} from "../actions";
import {Link} from "react-router-dom";
import {List,Stepper,Checkbox,Button} from "antd-mobile"
// const CheckboxItem = Checkbox.CheckboxItem;
import "./shoppingcar.less"
import Toast from "../components/Toast";
@connect(
    state=>{
        return {
            data:state.user
        }
    }
)
export class Shoppingcar extends React.Component{
    componentWillMount(){
    let {dispatch,history} = this.props;
       if(!localStorage.tel){
           
           history.push("/user")
       }else{
        http.get("/shoppingcar",{params:{tel:localStorage.tel}}).then(res=>{
            dispatch(getCar(res.data));
        })
       }

    }
    checkone=(value)=>{
        this.props.dispatch(setCarList({check:value.target.checked,dataId:value.target.dataId}))
    }
    checkall=(data)=>{
        this.props.dispatch(setQuan(data.target.checked));
    }
    add=(dataId,number)=>{
        let tel = localStorage.tel;
        this.props.dispatch(add(dataId,number,tel));
    }
    reduce=(dataId,number)=>{
        let tel = localStorage.tel;
        if(number==1){
            Toast.error("最小数量为一",1000)
        }else{
            this.props.dispatch(reduce(dataId,number,tel));
        }
    }
    deletes=()=>{
        this.props.dispatch(deletes(this.props.data.car));
    }
    jiesuan=()=>{
        if(this.props.data.total==0){
            Toast.error("请选择商品",1000)
        }else{
            this.props.dispatch(jiesuan());
            this.props.history.push("/jiesuan")
        }
        
    }
    detail=(key)=>{
        let {dispatch} = this.props;
        dispatch(getGood({url:"/search",key}));
    }
    render(){
        let {data:{car,carNum,total,quan}} = this.props;
        let dom =(<div className="null">
        <div>
            <img src="https://gw.alicdn.com/tfscom/TB1xdQSJFXXXXcuXXXXy7S8WFXX-176-176.png"/>
        </div>
    </div>)
    if(car.length>0){
        dom = car.map((good,i)=>{
            let{beizhu,dataId,hot,number,check,size,sweet,books:{body,title,preview}}=good
            if(!~preview.indexOf("https://www.starbucks.com.cn/")){
                preview="https://www.starbucks.com.cn/"+preview;
            }
            return (
                <li key={dataId}className="car">
                    <div className="left">
                        <img src={preview} />
                    </div>
                    <div className="right">
                    <Link to={dataId} onClick={()=>this.detail(dataId)}><h2>{title}</h2></Link>
                        <p>
                            {body}
                        </p>
                
                        <span>￥520</span>
                        <span>{size}</span>
                        <span>{sweet}</span>
                        <span>{hot}</span>
                    </div>
                    <p style={{textIndent:".3rem"}}>备注：{beizhu}</p>
                    <div style={{ width:"100%" ,height : '40px',lineHeight : "40px"}}>
                        <Checkbox checked={check} dataId={good.dataId} onChange={this.checkone} >
                        </Checkbox>
                        <span style={{fontSize:'22px',marginRight:'3px'}}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                        <span style={{fontSize:'22px',marginRight:'3px',backgroundColor : "#eee",borderRadius :"50%"}} onClick={()=>this.reduce(dataId,number)}>&nbsp;-&nbsp;</span>
                        <input dataId={dataId} type="text" value={number} style={{ width:"34px",fontSize:'16px',textAlign:'center',background: 'none',border:'0' }} />
                        <span style={{fontSize:'22px',marginRight:'3px',backgroundColor : "#eee",borderRadius :"50%"}} onClick={()=>this.add(dataId,number)}>+</span>      
                    </div>
                </li>    
                )
        })
    }

        
        return (
            
            <div>
                <div className="tx">
                <a onClick={()=>this.props.history.go(-1)}>
                        &nbsp;&nbsp;<img src="https://www.starbucks.com.cn/assets/icons/icon-chevron-left.svg"/>
                    </a>
                    购物车
                </div><ul>
                    {dom}
                    
                </ul>
                <div className="cars">
                    <div key="1" >
                        <div style={{ width:"18%" }}>
                            <Checkbox checked={quan} onChange={this.checkall} >   
                            </Checkbox>
                        </div>
                        <div style={{ width:"15%" }}>共{carNum}件</div>
                        <div style={{ width:"15%" }}>￥{total}</div>
                        <div style={{ width:"30%" ,display:"inline-block",lineHeight:".6rem"}}><Button type="warning" size="small" onClick={this.deletes}>删除选中</Button></div>
                        <div style={{ width:"18%",display:"inline-block",lineHeight:".6rem" }}><Button type="warning" size="small" onClick={this.jiesuan}>结算</Button></div>
                    </div>
                </div>
                
            </div>
        )
    }
}




