import "./cicle.less"
import "../autd.css"
import {connect} from "react-redux"
import { http } from "../utils";
import {  getGoods } from "../actions";
import { UserNav } from "../components/Cnav";
import {Button} from "antd-mobile"
import { GoodList } from "../components/GoodsList";
import {Link,withRouter} from "react-router-dom";
@withRouter
@connect(
    state=>{
        return {
            data:state.user
        }
    }
)
export class CicleList extends React.Component{
    state={
        item : '',
        list : []
    }

    componentWillMount(){
        let {dispatch} = this.props;
        var key = this.props.location.pathname.split("/cicle/")[1]
        http.get("/search",{params:{key:"/"+key+"/"}}).then(res=>{
            dispatch(getGoods(res.data));
        })
        switch(key){
            case "beverages": 
                this.setState({
                    item : "饮料",
                    list :[
                        {path:"/cicle/beverages",text:"全部"},
                        {path:"/cicle/beverages/espresso",text:"espresso"},
                        {path:"/cicle/beverages/teavana",text:"teavana"},
                        {path:"/cicle/beverages/frappuccino-blended-beverage",text:"frappuccino-blended-beverage"},
                        {path:"/cicle/beverages/signature-chocolate-beverage",text:"signature-chocolate-beverage"},
                        {path:"/cicle/beverages/coffee-plus-ice-cream",text:"coffee-plus-ice-cream"},
                        {path:"/cicle/beverages/cold-brew",text:"cold-brew"},
                        {path:"/cicle/beverages/nitro-cold-brew",text:"nitro-cold-brew"},
                    ]
                })
            break;
            case "merchandise": 
                this.setState({
                    item : "商品",
                    list :[
                        {path:"/cicle/merchandise",text:"全部"},
                        {path:"/cicle/merchandise/core-merchandise-2017",text:"core-merchandise-2017"},
                        {path:"/cicle/merchandise/reserve-store-merchandise",text:"reserve-store-merchandise"}
                    ]
                })
            break;
            case "coffee": 
                this.setState({
                    item : "咖啡产品",
                    list :[
                        {path:"/cicle/coffee",text:"全部"},
                        {path:"/cicle/coffee/whole-bean",text:"whole-bean"},
                        {path:"/cicle/coffee/starbucks-via",text:"starbucks-via"},
                        {path:"/cicle/coffee/starbucks-origami",text:"starbucks-origami"},
                        {path:"/cicle/coffee/starbucks-reserve",text:"starbucks-reserve"},
                    ]
                })
            break;
            case "food": 
                this.setState({
                    item : "美食",
                    list :[
                        {path:"/cicle/food",text:"全部"},
                        {path:"/cicle/food/sandwich",text:"sandwich"},
                        {path:"/cicle/food/cake-dessert",text:"cake-dessert"},
                        {path:"/cicle/food/bakery",text:"bakery"},
                        {path:"/cicle/food/yoghurt",text:"yoghurt"},
                        {path:"/cicle/food/package",text:"package"},
                    ]
                })
            break;
            default:
            break;
        }
    }
    
    star=()=>{
        let {dispatch} = this.props;
        var key = this.props.history.location.pathname.split("/cicle/")[1]
        http.get("/search",{params:{key:"/"+key+"/"}}).then(res=>{
            dispatch(getGoods(res.data));
        })
    }
    render(){
        let {data:{goods}} = this.props;
        return (
            <div>
                <div className="tx">
                <Link to="/cicle">
                        &nbsp;&nbsp;<img src="https://www.starbucks.com.cn/assets/icons/icon-chevron-left.svg"/>
                    </Link>
                    {this.state.item}
                </div>
                <div onClick={this.star}>
                    <UserNav list={this.state.list} />
                </div>
                <GoodList goods={goods} />
                <div className="sou" >
                    <Link to="/search">
                    <Button type="ghost" size="small" >
                        搜索商品
                    </Button></Link>
                </div>
                
            </div>
        )
    }
}




