import {HashRouter as Router,Route} from "react-router-dom"
import { Index } from "./page/Index";

import React, {Component} from "react";
export class IndexLayout extends Component {
    render(){
        return (
            <Router>
                <Route path="/" component={Index}></Route>
            </Router>
        )
    }
}


