import {NavLink} from "react-router-dom"
import "./footer.less"

export class Footer extends React.Component{
    constructor(props){
        super(props)
        this.state={
            list:[
                {path:"/home",text:"主页",icon:"iconfont icon-fangzi"},
                {path:"/user",text:"我的",icon:"iconfont icon-wodezhanghu"},
                {path:"/cicle",text:"菜单",icon:"iconfont icon-naichabeizi"},
                {path:"/shoppingcar",text:"购物车",icon:"iconfont icon-cart_icon"}
            ]
        }
    }
    render(){
        let domeList = this.state.list.map(({path,text,icon})=>
            <NavLink activeClassName="active" to={path} key={path}>
                <i className={icon}></i>
                <p>{text}</p>
            </NavLink>
        )
        
        return (<div className="footer">{domeList}</div>)
    }
}


